import $ from 'jquery';
import { Point } from "./Point";
import { Segment } from './Segment';
import { RandomText } from './RandomText';
import dat from 'dat.gui'

class IKMoji {
    
    mojis: HTMLElement[];
    tentacle: Segment;//触手
    lenTargets: number[];
    segs: Segment[];
    allSegs: Segment[];
    tgtX: number = 0;
    tgtY: number = 0;
    posX: number = 0;
    posY: number = 0;
    tmpX: number = 0;
    tmpY: number = 0;
    tgtLen: number = 10;
    scrollY: number = 0;
    isRandom: boolean = false;
    oldY: number = 0;
    subY: number = 0;
    scaleA: number=1;
    speedX: number=10;
    speedY: number=10;
    isFix: boolean=false;
    intervalId: number = 0;
    currentText: string = "";
    randomText: RandomText;
    distance: number = 0;
    count: number = 0;
    isPause: boolean = false;
    isFloor: boolean = false;

    constructor(text:string, parentId:string) {

        this.currentText = text;
        
        //var gui = new dat.GUI();
        //gui.add(this,"currentText").listen();

        this.posX=window.innerWidth/2;
        this.posY=window.innerHeight/2;
        this.tgtX=window.innerWidth/2;
        this.tgtY=window.innerHeight/2;

        this.mojis = [];
        this.lenTargets = [];
        this.segs = [];
        this.randomText = new RandomText();

        var link = document.getElementById(parentId);


        for(let i=0;i<=this.currentText.length+1;i++){
            
            //var d = document.getElementById("m"+i);
            var d = document.createElement("span");
            d.id = "m"+i;
            link.appendChild(d);
            this.mojis.push( d );

        }

        let point = new Point(
            window.innerWidth*Math.random(),
            window.innerHeight*Math.random()
        );
        
        let current = new Segment(point, 20, 0);
          
        
        for (let i = 0; i < this.mojis.length+1; i++) {    
            this.lenTargets.push(10);
            this.segs.push(current);
            
            let next = new Segment(current.b.Clone(), 20, i);
            
            current.child = next;
            current = next;
        
        }

        this.allSegs = this.segs;

        this.tentacle = current;
        this.changeParam();
        /*
        window.onclick = ()=>{
            this.changeParam();
        };
        $(window).scroll(()=>{
            this.changeParam();
            this.isFix=false;

            var doch = window.innerHeight; //ページ全体の高さ
            var winh = $(window).innerHeight(); //ウィンドウの高さ
            var hh = doch - winh; //ページ全体の高さ - ウィンドウの高さ = ページの最下部位置

            //console.log(doch,winh,$(window).scrollTop());
            //console.log($(window).scrollTop()-bottom);
            //console.log(hh,$(window).scrollTop());

            
            if($(window).scrollTop()+hh>=0){
                console.log("fin");  
                this.isFix=true;
            }

        });*/

        setInterval(()=>{
            
            //if($(window).scrollTop()==0)this.changeParam();
            this.changeParam();


        },1000);


        this.update();
        this.UpdateText();
    }


    UpdateText(){

        /*
        let rand = Math.random();
        if(rand<0.25){
            this.currentText="あのイーハトーヴォのすきとおった風";
        }else if(rand<0.5){
            this.currentText="うつくしい森で飾られたモリーオ市";
        }else if(rand<0.75){
            this.currentText="夏でも底に冷たさをもつ青いそら";
        }else{
            this.currentText="郊外のぎらぎらひかる草の波";
        }*/

        //this.currentText="10:29:46 Thu,August 13, 2020";
    
        //"あのイーハトーヴォのすきとおった風うつくしい森で飾られたモリーオ市夏でも底に冷たさをもつ青いそら郊外のぎらぎらひかる草の波/あのイーハトーヴォのすきとおった風うつくしい森で飾られたモリーオ市夏でも底に冷たさをもつ青いそら郊外のぎらぎらひかる草の波";

        this.randomText.Show(this.currentText);

        /*
        for(let i=0;i<=15;i++){

            if(i<this.currentText.length){
                this.mojis[i].textContent = this.currentText.substr(i,1);
            }else{
                this.mojis[i].textContent = "";
            }

        }*/

    }


    changeParam(){
  
        this.UpdateText();

        this.scrollY = 0;//$(window).scrollTop();
    
        //console.log("click " + this.scrollY );

    
        this.distance = Math.random() * 20 * this.scaleA + 20;
        if(Math.random()<0.05) this.distance = Math.random() * 100 *this.scaleA + 10;
    
        //console.log(this.lenTargets);

        for(var i=0;i<this.lenTargets.length;i++){
            this.lenTargets[i] = this.distance;
        }
        
        
        if(Math.random()<0.5){
            this.tgtX = window.innerWidth/2+ 0.7*window.innerWidth*(Math.random()-0.5);
            this.tgtY = window.innerHeight/2+ 0.7*window.innerHeight*(Math.random()-0.5);    
        }else{
            this.tgtX += window.innerWidth/5*(Math.random()-0.5);
            this.tgtY += window.innerHeight/5*(Math.random()-0.5);    
        }

        //this.scaleA=1.5;//+1.5*Math.random();
        for(var i=0;i<this.lenTargets.length;i++){
            this.lenTargets[i] = this.distance;
        }
            
        

    
        this.scaleA=3;
     
        //for(var i=0;i<segs.length;i++){
        //  segs[i].len = random() * 70;
        //}
        this.isRandom=Math.random()<0.5 ? true : false;
        this.speedX=15+20*Math.random();
        this.speedY=15+20*Math.random();
    
        
        //console.log(tgtX+" "+tgtY+" "+scaleA);
    
        for(var i=0;i<this.segs.length;i++){
            this.segs[i].setRandom(this.isRandom);
        }
    
    }


    Pause(){
        this.isPause=!this.isPause;
    }


    update(){


        if(!this.isPause){


            this.randomText.Update();
            
            let txt =this.randomText.text;
            
            for(let i=0;i<this.mojis.length;i++){
                let str = this.randomText.text.substr(i,1);
                if(i<this.randomText.text.length){
                    this.mojis[i].textContent = str;
                    if(str == " "){
                        this.mojis[i].style.display = "none";
                    }
                }else{
                    this.mojis[i].textContent = "";
                    this.mojis[i].style.display = "none";
                }

            }
                //if(i<this.currentText.length){
                //    this.mojis[i].textContent = this.currentText.substr(i,1);
                //}else{



            var dx = this.tgtX - this.posX;
            var dy = this.tgtY - this.posY;
            this.tmpX += dx/this.speedX;
            this.tmpY += dy/this.speedY;

            this.tmpX*=0.7;
            this.tmpY*=0.7;
            this.posX += this.tmpX;
            this.posY += this.tmpY;


            this.subY = Math.abs(document.documentElement.scrollTop-this.oldY);
            this.oldY = document.documentElement.scrollTop;

            //一番先端はposXをフォローする
            this.tentacle.follow(this.posX, this.posY);
            this.tentacle.update();

            for(let i=1;i<this.segs.length;i++){
                
                let ddx=0;
                let ddy=0;
                
                //反発力
                for(let j=0;j<this.allSegs.length;j++){
                    
                    let dx = this.allSegs[i].a.x - this.allSegs[j].a.x;
                    let dy = this.allSegs[i].a.y - this.allSegs[j].a.y;
                    
                    let distMax = this.distance * 0.9;
                    if(distMax>50) distMax=50;

                    if( dx!=0 && dy!=0 ){
                        let dd = Math.sqrt(dx*dx+dy*dy);
                        dd = dd / ( distMax );
                        if(dd>1) dd = 1;
                        ddx += 0.2 * ( dx * (1-dd) );
                        ddy += 0.2 * ( dy * (1-dd) );
                    }
                    
                }

                //先端以降はchilのaをフォローする
                this.segs[i].followChildA(ddx,ddy);
                this.segs[i].update();

            }


            for(let i=0;i<this.mojis.length;i++){

                let idx = this.segs.length-1-i;
                this.segs[idx].len += ( this.lenTargets[i] - this.segs[idx].len ) / 10;

                var xx = 0;//
                var yy = 0;//
                var rr = 0;


                if(this.isFix){
                    //footer
                    this.segs[idx].updateTgt( 
                        window.innerWidth/2+17*(i-this.currentText.length/2), 
                        window.innerHeight-105,0,10 
                    );
                    this.scaleA = 1;

                }else{
                    
                    this.segs[idx].updateTgt( this.segs[idx].b.x, this.segs[idx].b.y,this.segs[idx].angle / Math.PI * 180 + 180,2 );

                }

                

                
            }


            if(this.count%1==0){

                for(let i=0;i<this.mojis.length;i++){

                    let idx = this.segs.length-1-i;
                    xx = this.segs[idx].tgtX - this.mojis[i].clientWidth/2;
                    yy = this.segs[idx].tgtY;
                    rr = this.segs[idx].tgtR;

                    this.mojis[i].style.zIndex = ""+(i+200);
                    this.mojis[i].style.position = "fixed";
                    //this.mojis[i].style.backgroundColor = "#ff0";

                    //var grade = "radial-gradient(circle, rgba(0,255,0,1) 0%, rgba(0,0,0,1) 25%, rgba(0,255,255,1) 50%, rgba(0,0,0,1) 75%, rgba(255,0,255,1) 100%)";
                    var grade = "linear-gradient(90deg,rgba(255,255,255,0) 0%, rgba(0,0,255,0.4) 50%, rgba(255,255,255,0) 100%)";
                    if(this.currentText.indexOf("KITASENJU")>=0){
                        grade = "linear-gradient(90deg,rgba(255,255,255,0) 0%, rgba(255,0,0,0.4) 50%, rgba(255,255,255,0) 100%)";
                    }
                    this.mojis[i].style.background = grade;


                    this.mojis[i].style.borderRadius = "50%";
                    this.mojis[i].style.padding = "2px";
                    this.mojis[i].style.border = "1px solid blue";
                    this.mojis[i].style.borderColor = this.mojis[i].style.color;
                    
                    
                    this.mojis[i].style.margin = "20px";
                    
                    
                    if(!this.isFloor){
                        this.mojis[i].style.left = xx+"px";
                        this.mojis[i].style.top = yy+"px";
                        this.mojis[i].style.transform = "rotate(" + rr + "deg) scale("+this.scaleA+","+this.scaleA+")";
                    }else{

                        this.mojis[i].style.left = Math.floor(xx/15)*15 +"px";
                        this.mojis[i].style.top = Math.floor(yy/15)*15 +"px";
                        this.mojis[i].style.transform = "rotate(" + Math.floor(rr/45)*45 + "deg) scale("+this.scaleA+","+this.scaleA+")";
                        //this.mojis[i].style.transform = "rotate(" + 0 + "deg) scale("+this.scaleA+","+this.scaleA+")";

                    }

                }


            }

            this.count++;

        }


        window.requestAnimationFrame(this.update.bind(this));


    }

}

export {IKMoji};
